@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

@font-face {
    font-family: "Komika Axis";
    src: url("res/fonts/KOMIKAX_.ttf");
    }

@font-face{
    font-family: 'Wallpoet';
    src: url("res/fonts/wallpoet.regular.ttf");
}    



@font-face{
    font-family: 'Coolvetica';
    src: url("res/fonts/coolvetica compressed rg.ttf");
}    



* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    overflow-x: hidden;
    
}

html, body, #root {
    min-height: 100% !important;
}

body {
    background:  #0689AC /*linear-gradient( #83dff891, #f0f0f0);*/;
    background-size: cover;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    -webkit-font-smoothing: antialised !important;

}
body,input, button {
    font-family: 'Roboto', sans-serif;
}


::-webkit-scrollbar {
    width: 12px;
}
  
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: rgba(118, 228, 255, 0.8); 
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
    box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(118, 228, 255, 0.9);
}
