footer{
    width: 100%;
    background: #000;
    color: #fff;
    
}


footer span {
    margin-top: 10px;


}

.footer-container-wrapper {
}

footer .footer-container{
    width: 100%;
    padding-bottom: 20px;
    display: flex;
    justify-content: space-evenly;
    }

footer h2{
    letter-spacing: 6px;

}
footer div p {
    margin-top: 5px;
}
footer div p a{
    color: #333;
    text-decoration: none;
    transition: 0.5s;
}

footer div p a:hover{
    color: #fff;
}


footer .footer-bottom {
    border-top:1px solid #666;
    text-align: center;
    width: 100%
}

footer .footer-bottom .footer-bottom-items {
padding: 25px;
}

@media screen and (max-width: 768px){
    footer .footer-container{
        flex-direction: column;
        text-align: center;
        font-size: 17px;
    }
}


a.footer-link {
color: #fff

}

a.pocket-studio-title {
    color:#12f8af;
    font-size: 5em;
    font-family: 'Coolvetica';

}