@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@font-face {
    font-family: "Komika Axis";
    src: url(/static/media/KOMIKAX_.948114af.ttf);
    }

@font-face{
    font-family: 'Wallpoet';
    src: url(/static/media/wallpoet.regular.518e5534.ttf);
}    



@font-face{
    font-family: 'Coolvetica';
    src: url("/static/media/coolvetica compressed rg.b5687505.ttf");
}    



* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    overflow-x: hidden;
    
}

html, body, #root {
    min-height: 100% !important;
}

body {
    background:  #0689AC /*linear-gradient( #83dff891, #f0f0f0);*/;
    background-size: cover;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    -webkit-font-smoothing: antialised !important;

}
body,input, button {
    font-family: 'Roboto', sans-serif;
}


::-webkit-scrollbar {
    width: 12px;
}
  
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(118, 228, 255, 0.8);
    box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(118, 228, 255, 0.9);
}

body .background-opacity{
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.59;
    position: fixed;
}


body div.wrapper {
    max-width: 1040px;
    margin: 0 auto;
    padding: 50px;
}

div.wrapper div.content-over {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    color: #fff;
}
div.wrapper div.content-over img{
    width: 420px;
    margin: 50px auto;
}

div.wrapper div.content-over p#welcome-text{
    height: 100px;
    font-size: 50px;
}
div.wrapper div.content-over p{
    font-size: 18px;
}

div.wrapper div.content-over a#link-signup{
    font-family: 'Roboto';
    color: #5ccae7;
    font-size: 25px;
    font-weight: 700;
    margin: 10px;
    height: 100px;
}

div.wrapper div.content-over a#link-signup:hover{
    color: #8ddaf7;
    transition: 0.5s;

}
div.wrapper div.content-over p strong{
    font-family: "Wallpoet", sans-serif;
    color: #5ccae7;
    font-size: 60px;
    font-weight: normal;
    margin: 10px
}


body div.bottom-items{
    height: 100px;
    display: flex;
    justify-content: center;
    position: relative;
}

div.bottom-items a {
    color: #fff;
    margin-left: 40px;
    margin-right: 40px;
    text-decoration: none;

}

a#link-app-android{
    font-size: 25px;
    font-weight: bold;
    color: #a4e539;
}
a#link-app-android:hover{
    font-size: 30px;
    color: #a4f939;
    transition: 0.5s;
}

a#link-app-ios{
    font-size: 25px;
    font-weight: bold;
    color: #d0d0d0;
    cursor: not-allowed;
    transition: 0.5s;


}
a#link-app-ios:hover{
    font-size: 30px;
    font-weight: bold;
    color: #d0d0d0;
    cursor: not-allowed;

}



@media (max-width: 768px){

    div.wrapper div.content-over img{
        width: 280px;
    }

    
    div.wrapper div.content-over p#welcome-text{
        text-align: center;
        font-size: 35px;
    }

    div.wrapper div.content-over p strong {
        text-align: center;
        font-size: 35px;

    }

    div.wrapper div.content-over p {
        margin-top: 30px;
        text-align: center;
        font-size: 16px;
        font-weight: bold;
    }

    div.wrapper div.content-over a#link-signup {
        font-size: 25px;
    }

    body div.bottom-items {
        flex-direction: column;
        text-align: center;
        justify-content: space-between;
    }

    body div.bottom-items .left-side {
    }

    body div.bottom-items .right-side {
    }


    body div.bottom-items{
        height: auto;
    }

    body div.bottom-items div{
        margin: 10px;
    }


    a#link-app-android {
        height: 100px;
        font-size: 20px;
    }
    a#link-app-android:hover {
        height: 100px;
        font-size: 14px;
    }
    
    a#link-app-ios{
        font-size: 20px;
    }
    a#link-app-ios:hover{

        font-size: 14px;
        
    }
}
.signup {
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 30px;
    display: flex;
}

aside {
    position: relative;
    width: 420px;
    background: #fff;
    border: 1px solid #999;
    border-radius: 2px;
    padding: 30px 20px;

}

.signup #formLogo{
    position: relative;
    width: 200px;
    display: flex;
    margin: 0 auto;
}

span.signup-title {
    font-size: 2em;
    font-style: normal;
    text-decoration: none;
    font-weight: bold;
    color: #5ccae7;
    text-align: center;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.signup form{
    margin-top: 30px; 
    position: relative;
}

.signup form .v_inputs{
    display: flex;
    flex-direction: column;
}

.signup form label{
    margin-top: 10px;
    color: #666;
    font-weight: bold;
}


.signup form input {
    width: 100%;
    height: 32px;
    border: 0;
    border-bottom: 1px solid #eee;
    font-size: 14px;
    color: #666;
}

.signup form input::-webkit-input-placeholder{
    color: #999;
    opacity: 0.5;
}

.signup form input:-ms-input-placeholder{
    color: #999;
    opacity: 0.5;
}

.signup form input::-ms-input-placeholder{
    color: #999;
    opacity: 0.5;
}

.signup form input::placeholder{
    color: #999;
    opacity: 0.5;
}

.signup form button[type=submit]{
    width: 100%;
    margin-top: 30px;
    border: 0;
    border-radius: 2px;
    background: #5ccae7;
    font-size: 16px;
    color:#fff;
    font-weight: bold;
    padding: 10px 15px;
    cursor: pointer;
    transition: 0.1s;
}

.signup form button[type=submit]:hover{
    background: #5dc3e7;
}

.signup form p {
    margin-top: 5px;
    font-size: 13px;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.signup form p #linkToTermos{
    color: #5dc3e7;
}

.signup form p #linkToTermos:hover{
    color: #5dc3f7;
}


/*Section MAIN */

main {
    position: relative;
    flex: 1 1;
    overflow: scroll;
    overflow-x: hidden;
    margin-left: 10px;
    padding-right: 10px;
}

div.text-confira strong {
    position: absolute;
    font-size: 16px;
    margin-left: 10px;
    margin-top: -20px;
    color: #fff;
}

main header img {
    width: auto;
    height: auto;
    padding: 20px;
}

main header .header-wrapper {
    display: flex;
    justify-content: center;
    margin-left: 10px;
    height: 250px;
}

main header .header-wrapper .block-header{
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    margin: 10px 5px;

}

main ul {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-gap: 20px;
    gap: 20px;
    max-height: 500px;
}

main ul li.company-key{
    background: rgba(999,999,999, 0.9);
    box-shadow:  0 0 11px 0 #5cc9e78f;
    border-radius: 10px;
    text-align: center;
    padding: 20px;
    
}

main ul li.company-key:nth-child(1), main ul li.company-key:nth-child(2){
    margin-top: 1px;
}

li.company-key strong {
    font-size: 14px;
    color: #333;
    margin-top: 20px;
}
li.company-key span {
    font-size: 13px;
    color: #999;
}


@media (max-width: 1060px){
    .signup {
        flex-direction: column;
    }
    aside {
        width: 100%;
    }

    div.text-confira {
    margin-top: 30px;
    text-align: center;
    }

    div.text-confira strong {
        position: relative;
        font-size: 16px;
        /* margin-left: 10px; */
        margin-top: -20px;
        color: #fff;
    }


    main{
        margin-top: 30px;
    }

}

@media (max-width: 620px){

    main ul {
        grid-template-columns: 1fr;
    }
    


}
.header-container {
    width: 100%;
    height: 90px;
    background-color: #000;
    color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    }


.header-nav{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 20px;
    height: 100%;
    text-align: center;
}

.header-nav a {
    color: #fff;
    text-decoration: none;
    padding: 10px;
    transition: 0.2s;
}

.header-nav a:nth-child(1) {
    border: none;
}
.header-nav a:hover {
    background: #5ccae7;
}
.header-container img {
    padding: 5px;
}


@media screen and (max-width: 768px){
    .header-container {
    display: none;
    }
}



/* SVG LOGO AJUSSTES */
.mobile-trigger{
    display: none;
}
.mheader-container {
    display: none;
    width: 0px;
    height: auto;
    background: #000;
    position: absolute;
    right: 0;
    transition: 0.1s;
}
.mobile-trigger img {
    display: none;
}

.mheader-container a img {
    width: 100%;
    display: flex;
}

.mheader-container .mheader-nav {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.mheader-container .mheader-nav a {
    padding: 15px;
}

@media screen and (max-width: 768px){
    .mobile-trigger{
        width: 100%;
        height: 59px;
        background: #000;
        color: #fff;
        font-size: 40px;
        display: flex;
        justify-content: center;
    }
    .mobile-trigger img {
        width: 200px;
        display: flex;
        padding: 10px;
    }




    .mobile-trigger span {
        transform: rotate(90deg);
        margin: 5px;
        display: flex;
        align-items: center;
        vertical-align: middle;
        cursor: pointer;
        position: absolute;
        right: 0;
    }
    .mheader-container {
        display: flex;
        color: aliceblue;
        z-index: 9999999999;
}
    .mobile-menu-header-wrapper{
        display: flex;
        margin: 10px;
        margin-bottom: 1px solid #fff;
    }

    .mheader-nav a{
        color: #5ccae7;
        text-decoration: none;

    }
    .mobile-menu-header-wrapper p {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        cursor: pointer;
    }

}
footer{
    width: 100%;
    background: #000;
    color: #fff;
    
}


footer span {
    margin-top: 10px;


}

.footer-container-wrapper {
}

footer .footer-container{
    width: 100%;
    padding-bottom: 20px;
    display: flex;
    justify-content: space-evenly;
    }

footer h2{
    letter-spacing: 6px;

}
footer div p {
    margin-top: 5px;
}
footer div p a{
    color: #333;
    text-decoration: none;
    transition: 0.5s;
}

footer div p a:hover{
    color: #fff;
}


footer .footer-bottom {
    border-top:1px solid #666;
    text-align: center;
    width: 100%
}

footer .footer-bottom .footer-bottom-items {
padding: 25px;
}

@media screen and (max-width: 768px){
    footer .footer-container{
        flex-direction: column;
        text-align: center;
        font-size: 17px;
    }
}


a.footer-link {
color: #fff

}

a.pocket-studio-title {
    color:#12f8af;
    font-size: 5em;
    font-family: 'Coolvetica';

}
